<template><section><h1>绘制工具</h1>
<ul>
<li><code v-pre>vc-handler-draw-point</code> 组件用于绘制点。</li>
<li><code v-pre>vc-handler-draw-polyline</code> 组件用于绘制线。</li>
<li><code v-pre>vc-handler-draw-polygon</code> 组件用于绘制面。</li>
</ul>
<h2>示例</h2>
<h3>绘制线</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" scene3DOnly>
        <vc-handler-draw-point ref="handlerPoint" @activeEvt="activeEvt" @movingEvt="movingEvt" @drawEvt="drawEvt"></vc-handler-draw-point>
        <vc-handler-draw-polyline :clampToGround="clampToGround" ref="handlerLine" @activeEvt="activeEvt" @movingEvt="movingEvt" @drawEvt="drawEvt"></vc-handler-draw-polyline>
        <vc-handler-draw-polygon :clampToGround="clampToGround" ref="handlerPolygon" @activeEvt="activeEvt" @movingEvt="movingEvt" @drawEvt="drawEvt"></vc-handler-draw-polygon>
        <vc-primitive-tileset :url="modelUrl" @readyPromise="readyPromise"></vc-primitive-tileset>
      </vc-viewer>
      <div class="demo-tool">
        <md-button class="md-raised md-accent" @click="toggle('handlerPoint')">{{ pointDrawing ? '停止' : '点' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('handlerLine')">{{ polylineDrawing ? '停止' : '线' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('handlerPolygon')">{{ polygonDrawing ? '停止' : '面' }}</md-button>
        <md-button class="md-raised md-accent" @click="clampToGround = !clampToGround">贴地</md-button>
        <md-button class="md-raised md-accent" @click="clear">清除</md-button>
      </div>
    </div>
  </template>
  
  
</doc-preview>
