<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">modelUrl</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/Cesium3DTiles/Tilesets/Tileset/tileset.json'</span>,
        <span class="hljs-attr">pointDrawing</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">polylineDrawing</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">polygonDrawing</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">clampToGround</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">var</span> scene = viewer.scene
        scene.debugShowFramesPerSecond = <span class="hljs-literal">true</span>
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">this</span>.tooltip = createTooltip(viewer.cesiumWidget.container)
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
      },
      toggle(type) {
        <span class="hljs-built_in">console</span>.log(type)
        <span class="hljs-keyword">this</span>.$refs[type].drawing = !<span class="hljs-keyword">this</span>.$refs[type].drawing
      },
      clear() {
        <span class="hljs-keyword">this</span>.$refs.handlerPoint.clear()
        <span class="hljs-keyword">this</span>.$refs.handlerLine.clear()
        <span class="hljs-keyword">this</span>.$refs.handlerPolygon.clear()
      },
      activeEvt(_) {
        <span class="hljs-keyword">this</span>[_.type] = _.isActive
      },
      movingEvt(windowPosition) {
        <span class="hljs-keyword">this</span>.tooltip.showAt(windowPosition, <span class="hljs-string">'&lt;p&gt;左键绘制, 右键结束绘制.&lt;/p&gt;'</span>)
      },
      drawEvt(result) {
        result.finished &amp;&amp; <span class="hljs-keyword">this</span>.tooltip.setVisible(<span class="hljs-literal">false</span>)
        <span class="hljs-built_in">console</span>.log(result)
      },
      readyPromise(tileset) {
        <span class="hljs-keyword">const</span> { viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.zoomTo(tileset, <span class="hljs-keyword">new</span> Cesium.HeadingPitchRange(<span class="hljs-number">0.0</span>, <span class="hljs-number">-0.5</span>, tileset.boundingSphere.radius * <span class="hljs-number">2.0</span>))
      }
    }
  }
