</thead>
<tbody>
<tr>
<td>mode</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 绘制模式，0 连续绘制，1 绘制一次就结束。</td>
</tr>
<tr>
<td>pointColor</td>
