</tr>
<tr>
<td>pointPixelSize</td>
<td>Number</td>
<td><code v-pre>8</code></td>
<td><code v-pre>optional</code> 指定点的像素大小。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定绘制的点是否可见。</td>
</tr>
